<template>
    <div class="bg">

        
        <div class="content">
            <el-row class="p20 font16 line">
                <el-col :span="8" class="column">
                    <i class="iconfont icon-class"></i>
                    <p class="font22 pt5">1</p>
                    <span>已售罄商品数</span>
                </el-col>
                <el-col :span="8" class="column">
                     <i class="iconfont icon-daifahuo"></i>
                    <p class="font20 pt5">2</p>
                    <span>待发货商订单</span>
                </el-col>
                <el-col :span="8" class="column">
                     <i class="iconfont icon-weiquanzhong"></i>
                     <p class="font20 pt5">2</p>
                    <span>维权中订单</span>
                </el-col>
            </el-row>
              <el-row class="p20 font16 line">
                <el-col :span="8" class="column">
                    <i class="iconfont icon-yisheng"></i>
                    <p class="font22 p10">1</p>
                    <span>医生在线数量</span>
                </el-col>
                <el-col :span="8" class="column">
                     <i class="iconfont icon-wode-xinxiaoxitongzhi"></i>
                    <p class="font20 p10">2</p>
                    <span>新增消息通知</span>
                </el-col>
                <el-col :span="8" class="column">
                     <i class="iconfont  icon-zhibobofangshexiangjitianxianxianxing"></i>
                     <p class="font20 p10">2</p>
                    <span>线上直播人数</span>
                </el-col>
            </el-row>
        </div>
           <div class="content p20">
              <div class="row"> 
                  <p class="flexLeft"><i class="iconfont icon-jilu blue pr10"></i>订单概述</p>
                <el-tabs v-model="activeName" @tab-click="handleClick" >
                    <el-tab-pane label="今日" name="first"></el-tab-pane>
                    <el-tab-pane label="昨日" name="second"></el-tab-pane>
                    <el-tab-pane label="最近7日" name="third"></el-tab-pane>
                    <el-tab-pane label="本月" name="fourth"></el-tab-pane>
                </el-tabs>
              </div>
                <el-row class="p20 font16 ">
                    <el-col :span="8" class="column">
                        <p class="font22 p10 yellow">1/1</p>
