                    </el-col>
                    <el-col :span="8" class="column">
                       <p class="font22 p10 yellow">1/1</p>
                        <span>成交额/交易额</span>
                    </el-col>
                    <el-col :span="8" class="column">
                        <p class="font20 p10 yellow">2</p>
                        <span>人均消费</span>
                    </el-col>
                </el-row>
        </div>
  <div class="content p20">
      <div  id="myEchart" style="height:300px;width:100%;background:#fff" ref="myEchart"></div>
    </div>
    </div>
	
</template>

