    //        data:  this.chartData.data
    //      }]
    //    })
    //  },
     initChart(){
        let myChart = echarts.init(document.getElementById('myEchart'));
        myChart.setOption({
          
          title: {
              text: '近七日交易走势',
              subtext: '',
              color:"#fff"
          },
          tooltip: {
              trigger: 'axis'
          },
          legend: {
              data: ['交易量', '成交量',"交易额","成交额"]
          },
          toolbox: {
              show: true,
              feature: {
                  dataZoom: {
                      yAxisIndex: 'none'
                  },
                  dataView: {readOnly: false},
                  magicType: {type: ['line', 'bar']},
                  restore: {},
                  saveAsImage: {}
              }
          },
          xAxis: {
              type: 'category',
              boundaryGap: false,
              data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value}'
              }
          },
          series: [
              {
                  name: '交易量',
                  type: 'line',
                  data: [11, 11, 15, 13, 12, 13, 10],
                  markPoint: {
                      data: [
                          {type: 'max', name: '最大值'},
                          {type: 'min', name: '最小值'}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'}
                      ]
                  }
              },
              {
                  name: '成交量',
                  type: 'line',
                  data: [1, -2, 2, 5, 3, 2, 0],
                  markPoint: {
                      data: [
                          {name: '周最低', value: -2, xAxis: 1, yAxis: -1.5}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'},
                          [{
                              symbol: 'none',
                              x: '90%',
                              yAxis: 'max'
                          }, {
                              symbol: 'circle',
                              label: {
                                  position: 'start',
                                  formatter: '最大值'
                              },
                              type: 'max',
                              name: '最高点'
                          }]
                      ]
                  }
              },
