                      data: [
                          {type: 'average', name: '平均值'}
                      ]
                  }
              },
              {
                  name: '成交额',
                  type: 'line',
                  data: [1, 4, 26, 5, 13, 2, 0],
                  markPoint: {
                      data: [
                          {name: '周最低', value: -2, xAxis: 1, yAxis: -1.5}
                      ]
                  },
                  markLine: {
                      data: [
                          {type: 'average', name: '平均值'},
                          [{
                              symbol: 'none',
                              x: '90%',
                              yAxis: 'max'
                          }, {
                              symbol: 'circle',
                              label: {
                                  position: 'start',
                                  formatter: '最大值'
                              },
                              type: 'max',
                              name: '最高点'
                          }]
                      ]
                  }
              }
          ]
        });
     },



     
    },
	created(){
     
    },
    //数据自动刷新，必然需要一个监听机制告诉Echarts重新设置数据
    watch: {
     
    },
  components:{
       

    },

   mounted: function () {
  
    this.initChart();
  },
    computed:{
     ...mapGetters({
     
    }),
    },
 
}
</script>



